    			btn : '了解详情'
    		}
    	],
      carousel : [
    		{
    			src : carousel_item,
    			desc : '企业将智能进行到底，本公司所有模板、功能全部智能。智能提供无上限的空间容量、无上限网站流量，智能支持自有域名绑定，智能提供网站域名备案',
    			title : '古月',
    		},
    		{
    			src : carousel_item,
    			desc : '将智能进行到底，本公司所有模板、功能全部智能。智能提供无上限的空间容量、无上限网站流量，智能支持自有域名绑定，智能提供网站域名备案',
    			title : '古月',
    		},
    		{
    			src : carousel_item,
    			desc : '将智能进行到底，本公司所有模板、功能全部智能。智能提供无上限的空间容量、无上限网站流量，智能支持自有域名绑定，智能提供网站域名备案',
    			title : '古月',
    		}
